@import url(https://fonts.googleapis.com/css2?family=Bungee+Inline&family=Great+Vibes&family=Titillium+Web:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbrand{
    font-family: 'Bungee Inline', cursive;
}
.color-nav{
    font-family: 'Titillium Web', sans-serif;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 700;
    letter-spacing: 2px;
    background: linear-gradient(90deg,rgb(216, 210, 215),rgb(236, 226, 234));
}
.navbar-light .navbar-nav .nav-link{
    color: rgb(30, 30, 56);
}
.navbar-light .navbar-nav .nav-link:active,
.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link:hover{
    color: rgb(87, 12, 53);
}
.bgcontact{
    background: url(/travel/static/media/banner3.271cd9fb.jpg);
    background-size: cover;
    min-height: 100vh;
}
h6{
    font-family: 'Titillium Web', sans-serif;
}

